.meme-item {
	text-align: left;
	display: inline-block;
	padding: 10px;
	position: relative;
	width: 150px;
	height: 150px;
	margin: 5px;
	cursor: pointer;
}

.meme-img {
	width: 150px;
	height: 150px;
	border-radius: 8px;
	border: 3px solid #fff;
	position: absolute;
	object-fit: cover;
	filter: brightness(90%);
}

.meme-txt {
	width: 150px;
	text-align: center;
	position: absolute;
	border-radius: 5px;
	bottom: 0;
	animation-name: fadeIn;
	animation-duration: 1s;
	font-size: 14px;
}

.no-txt {
	display: none;
}

.darken-img {
	background-color: grey;
	filter: brightness(10%);
	animation-name: darken;
	animation-duration: 1s;
}

@keyframes darken {
	from {
		filter: brightness(90%);
	}
	to {
		filter: brightness(10%);
	}
}

@keyframes fadeIn {
	from {
		opacity: 0;
	}
	to {
		opacity: 100;
	}
}
